body {
  font-family: 'Nunito';
}

h2 {
  font-size: 18px;
  padding: 10px 20px;
  color: #575ed8;
}

.window {
  min-height: 500px;
  max-height: 500px;
  max-width: 600px;
  margin: 30px auto;
  border: 1px solid #ddd;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
}

.dot {
  display: inline-block;
  border-radius: 50%;
  width: 50%;
  background-color: #eee;
  height: 10px;
  width: 10px;
  margin: 0 2px;
}

.dot-success {
  background-color: #449d44;
}

.window-head {
  background: #f9f9f9;
  font-weight: bold;
}

.window-head h2 {
  max-width: 150px;
  margin: auto;
}

.list {
  max-height: 400px;
  margin: 20px 10px;
  overflow: auto;
}

#mario-chat {
  max-width: 600px;
  margin: 30px auto;
  border: 1px solid #ddd;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.05);
  border-radius: 2px;
}

#chat-window {
  height: 400px;
  overflow: auto;
  background: #f9f9f9;
}

#output p {
  padding: 14px 0px;
  margin: 0 20px;
  border-bottom: 1px solid #e9e9e9;
  color: #555;
  display: block;
}

#feedback p {
  color: #aaa;
  padding: 14px 0px;
  margin: 0 20px;
}

#output strong {
  color: #575ed8;
}

label {
  box-sizing: border-box;
  display: block;
  padding: 10px 20px;
}

input {
  padding: 10px 20px;
  box-sizing: border-box;
  background: #eee;
  border: 0;
  display: block;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #eee;
  font-family: Nunito;
  font-size: 16px;
}

#send {
  background: #575ed8;
  color: #fff;
  font-size: 18px;
  border: 0;
  padding: 12px 0;
  width: 100%;
  border-radius: 0 0 2px 2px;
}

.leaveBtn {
  width: 15%;
}

.right-0 {
  display: inline-block;
}

.mw {
  min-width: 60%;
  display: inline-block;
}

.mw-50 {
  /* min-width: 40%; */
  display: inline-block;
}

.container {
  margin-top: 50px;
  max-width: 70%;
}

h1 {
  text-align: center;
}

